import { closeCookiesModal } from '../modules/howoge/closeCookiesModal.js'
import { waitForNavigation } from './puppeteer/waitForNavigation.js'

export async function navigateToFlatOfferListPage (page, flatOffersUrl) {
  await Promise.all([
    waitForNavigation(page),
    page.goto(flatOffersUrl),
  ])
  await closeCookiesModal(page)
}
